export class WalletError extends Error {
    constructor(message, error) {
        super(message);
        this.error = error;
    }
}
export class WalletNotFoundError extends WalletError {
    constructor() {
        super(...arguments);
        this.name = 'WalletNotFoundError';
    }
}
